<template>
    <div class="container--text-center" id="SignUp">
      <div class="header--nav">
        <img class="header__image--nav" src="../../../assets/slow_logo.png" alt="SLoW">
        <div class="header__slogan--nav">
          Smart Life on the Web
        </div>
      </div>
      <div v-if="!isPolicyAgree" class="body">
        <div class="body__title">
          <h2>  </h2>
          <span>    .</span>
        </div>
        <div class="body__contents">
          <pre v-text="policy"></pre>
          <input type="checkbox" v-model="isCheckPolicy" id="policy">
          <label for="policy"><i class="fa fa-check-circle"></i>   .</label>
          <pre v-text="privacyPolicy"></pre>
          <input type="checkbox" v-model="isCheckPrivacyPolicy" id="privacyPolicy">
          <label for="privacyPolicy"><i class="fa fa-check-circle"></i>   .</label>
          <input type="submit" @click="policyAgree()" value="">
        </div>
      </div>
      <div v-else class="body">
        <div class="body__title">
          <h2></h2>
          <span>      .</span>
        </div>
        <div class="body__contents">
          <input type="text" placeholder="  ">
          <input type="button" value="">
          <input type="text" placeholder="">
          <input type="text" placeholder="">
          <input type="password" placeholder="">
          <input type="password" placeholder=" ">
          <router-link :to="{path: '/auth/sign-in'}">
            <input type="submit" value="">
          </router-link>
        </div>
      </div>
      <div class="footer">
        Copyright2017 by TechAnd Inc. All rights Reserved.
      </div>
    </div>
</template>

<script>
  import * as Policy from './Policy'

  export default {
    name: "SignUp",
    data() {
      return {
        isCheckPolicy: false,
        isCheckPrivacyPolicy: false,
        isPolicyAgree: false,
      }
    },
    computed: {
      policy() {
        return Policy.policy;
      },
      privacyPolicy() {
        return Policy.privacyPolicy;
      }
    },
    methods: {
      policyAgree() {
        if(this.isCheckPolicy && this.isCheckPrivacyPolicy) this.isPolicyAgree = true;
        else{
          alert("   .");
          this.isPolicyAgree = false;
        }
      }
    }
  }
</script>

<style src="../../../style/common/common.css" scoped></style>
<style src="../../../style/common/input.css" scoped></style>
<style src="../../../style/spa/Auth/SignUp/SignUp.css" scoped></style>



